<template>
  <div class="bgfff">
    <!--轮播i图-->
    <el-carousel :interval="0" arrow="always" height="240px"  style="border-radius: 15px;margin: 15px;margin-top: 45px;background: #fff;box-shadow:rgba(136, 136, 136, 0.42) 0px 0px 10px">
      <el-carousel-item v-for="item in 1" :key="item">
        <div class="item">
          <img src="../../../../static/img/home/mainimg.png" style="width: 100%;height: 200px">
          <div style="line-height: 40px;text-align: center;color: #1B1B1B;font-family:PingFang-SC-Medium;font-size: 18px;background: #fff;font-weight: bold">郑博文-商魂</div>
        </div>


      </el-carousel-item>
    </el-carousel>

    <div class="flex nav">
      <div class="flex1">商业财经</div>
      <div class="flex1">商业财经</div>
      <div class="flex1">商业财经</div>
      <div class="flex1">商业财经</div>
      <div class="flex1"><i class="el-icon-caret-bottom"></i></div>
    </div>
    <div class="flex nav">
      <div class="flex1">商业财经</div>
      <div class="flex1">商业财经</div>
      <div class="flex1">商业财经</div>
      <div class="flex1">商业财经</div>
      <div class="flex1" style="visibility: hidden">商业财经</div>
    </div>

    <!--畅销课程-->
    <div class="main1 mt15 bgfff">
      <div class="list text-tc radius" style="width: 100%">
        <el-row :gutter="2" style="margin: 0" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
          <el-col :span="12"  v-for="(item,index) in list" :key="index">
            <div class="item text_tc">
              <div class="imgs">
                <img src="../../../../static/img/home/mainimg0.png" style="width: 100%;height: auto">
                <img src="../../../../static/img/home/fufei.png" class="fufei">
              </div>
              <p>文字不懂文字不懂文字不懂文字不懂</p>
              <div class="Stars" style="text-align: left">
                <img  src="../../../../static/img/home/Stars.png">
                <img  src="../../../../static/img/home/Stars.png">
                <img  src="../../../../static/img/home/Stars.png">
                <img  src="../../../../static/img/home/Stars.png">
                <img  src="../../../../static/img/home/Stars.png">
              </div>


            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <!--畅销课程加载更多-->
    <div class="pageLoad" v-if="pageLoad">
      <i class="el-icon-loading"></i>
      上划自动加载
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        activeName:"first",
        input5:"",
        list:[1,2,3,4],
        pageLoad:false,


        Recommendcourses:{    //推荐课程
          list:[],
          load:false
        }

      }
    },
    methods:{
      handleClick(e){
      },
      loadMore() {
        this.pageLoad = true;
        setTimeout(() => {
          let last = this.list[this.list.length - 1];
          for (let i = 1; i <= 2; i++) {
            this.list.push(last + i);
          }
          this.pageLoad = false;
        }, 2500);
      },

    },
    mounted(){

    },
  }
</script>


<style lang="less" scoped>
  .nav .flex1{
    background: #EEEEEE;
    color: #1B1B1B;
    text-align: center;
    padding:10px  5px;
    border-radius: 10px;
    font-size: 12px;
    margin: 5px;
  }
</style>



